<template>
	<div>
		<p class="title">{{list.title}}</p>
		<div v-for="(item,index) in arr" class="item" :style="{height:item.height}"  @click="showList(index,item)">
			<div class="left" >
				<div class="btn" >{{item.btn_text}}</div>
			</div>
			<div class="right">
				<p  style="font-size: 0.32rem;margin-bottom: 0.2rem;">{{item.title}}</p>
				<p  v-for="littleItem in item.minutia" class="active" @click.stop="go(littleItem.id)">{{littleItem.title}}</p>
				
			</div>
			
		</div>
		
		
	<!--<div :class="['item',show?'showItem':'']  ">
			<div class="left">
				<div class="btn" @click="showList">{{btn_text}}</div>
			</div>
			<div class="right">
				<p style="font-size: 0.32rem;margin-bottom: 0.2rem;">第一章 标题, 30字内</p>
				<p class="active">1.01 小节</p>
				<p class="active">1.01 小节</p>
				<p class="active">1.01 小节</p>
			</div>
			
		</div>
		<div :class="['item',show?'showItem':'']  ">
			<div class="left">
				<div class="btn" @click="showList">{{btn_text}}</div>
			</div>
			<div class="right">
				<p style="font-size: 0.32rem;margin-bottom: 0.2rem;">第一章 标题, 30字内</p>
				<p class="active">1.01 小节</p>
				<p class="active">1.01 小节</p>
				<p class="active">1.01 小节</p>
			</div>
			
		</div>-->
		
		
	</div>
</template>

<script>
	
	export default{
		data(){
			return {
				
				
				list:{},
				arr:[],
				type:this.$route.params.type
			
			}
		},
		
		created(){
			
			
		},
		
		mounted(){
			this.arr=[]
			var obj={
				"s":this.$store.state.sessionId
			}
			
			this.$Api.CourseList(obj).then((res)=>{
			   console.log(res)
				this.list=res.q.courses
				
				for(var i=0;i<res.q.courses.section.length;i++){
					var obj={
						show:true,
						btn_text:"+",
						id:res.q.courses.section[i].id,
						minutia:res.q.courses.section[i].minutia,
						num:res.q.courses.section[i].num,
					    height:"0.45rem",
						title:res.q.courses.section[i].title
						
					}
					this.arr.push(obj)
				}
				
			})
			
		},
		methods:{
			showList(index,obj){
				var _this=this
				obj.show=!obj.show
			   this.$set(this.arr,index,obj)
//				_this.arr[index].show=!_this.arr[index].show
				
				if(_this.arr[index].show){
					_this.arr[index].btn_text='+'
					_this.arr[index].height="0.45rem"
				}else{
					_this.arr[index].btn_text="-"
					_this.arr[index].height=(_this.arr[index].minutia.length*0.6+0.62)+"rem"
				}
				
				
			},
			go(id){
				if(this.type=="留言"){
						this.$router.push({name: 'ConsolidateItem',params:{ConsoledateId:id}})
				}else{
						this.$router.push({name: 'Clear',params:{ConsoledateId:id}})
				}
			
				
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.title{
	padding: 0.3rem;
	font-size: 0.34rem;
	width: 100%;
	float: left;
}
.item{
width: 100%;
	float: left;
transition: 0.5s;																		
margin-bottom: 0.3rem;
	overflow: hidden;
	.left{
		width: 10%;
		padding-left: 0.3rem;
		box-sizing: border-box;
		float:left;
		.btn{
			width: 0.45rem;
			height: 0.45rem;
			background: #fa8a12;
			font-size: 0.5rem;
			text-align: center;
			line-height: 0.45rem;
			color: #FFFFFF;
			border-radius:50% ;
		}
	}
	.right{
		width: 90%;
		float: left;
		padding-left: 0.2rem;
		box-sizing: border-box;
		font-size: 0.28rem;
		
	}
	.active{
		color: #fa8a12;
	}
}
.showItem{
	height: 0.45rem;

}

</style>


